:root {
  --mainBg: mediumpurple;
}

$light-text: moccasin;

body {
  background-color: mediumpurple;
  background-color: var(--mainBg);
  color: $light-text;
}

.container {
  padding: 1rem;
  display: flex;
  background: linear-gradient(to top, black, white);
  h1 {
    font-size: 68px;
    text-align: center;
  }
}
